import React from 'react'
import styled from 'styled-components'
import { Grid } from '../layout/GridSystem'
import tokens from '@/styles/tokens.json'

const { semantic: { spacing } } = tokens

const DesignGridContainer = styled(Grid)`
  gap: ${spacing.layout.lg};
  margin: ${spacing.layout.lg} 0;
`

interface DesignGridProps {
  children: React.ReactNode
  'data-testid'?: string
}

export const DesignGrid: React.FC<DesignGridProps> = ({ 
  children, 
  'data-testid': testId 
}) => {
  return (
    <DesignGridContainer data-testid={testId}>
      {children}
    </DesignGridContainer>
  )
}